<template>
  <fixed-layout>
    <template slot="header">
      <topbar>
        <template slot="before">
          <b class="iconfont icon-back" @click="$router.go(-1)"></b>
        </template>
        
        <template slot="after">
          <button class="btn" @click="handNext"></button>
        </template>
      </topbar>
    </template>
    <section class="input-box">
      <input v-model="title" style="font-size: 24px;" type="text" placeholder=""/>
    </section>
    <section class="input-box">
      <textarea v-model="summary" placeholder="25"></textarea>
    </section>
    <div id="editor"></div>
  </fixed-layout>
</template>
<script>
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'
  import Quill from 'quill'
  import 'quill/dist/quill.bubble.css'

  let quill = null
  export default {
    name: '',
    components: {
      FixedLayout,
      Topbar
    },
    props: {},
    data () {
      return {
        cover: '',
        title: '',
        summary: '',
        content: ''
      }
    },
    watch: {},
    computed: {},
    created () {
      this.init()
    },
    mounted () {
      quill = new Quill('#editor', {
        modules: {
          toolbar: [
            [{header: [1, 2, false]}],
            ['bold', 'italic', 'underline'],
            ['image']
          ]
        },
        formats: {
          size: '120px',
          bold: true,
        },
        placeholder: '',
        theme: 'bubble'
      })
    },
    methods: {
      init () {
      },
      initData () {},
      handNext () {
        this.content = quill.getText()
        this.$store.commit(this.$actionType.write.set_article_data, {
          title: this.title,
          summary: this.summary,
          content: this.content
        })
        this.$router.push({
          path: 'preview'
        })
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .fixed-layout {
    background-color: #fff;
  }

  .input-box {
    padding: 10px;
    border-bottom: dashed #d7d7d7 1px;
    input,
    textarea {
      width: 100%;
      border-color: transparent;
    }
  }

  #editor {
    font-size: 16px;
  }
</style>



